export default {
	data() {
		return {
			text:["正在借阅","借阅历史"],
			TabCur: 0,
			scrollLeft: 0,
			userInfo:{},
		}
	},
	onShow() {
		var me = this;
		var userInfo = uni.getStorageSync("globalUser");
		me.userInfo = userInfo;
		console.log(this.userInfo);
	},
	methods: {
		tabSelect(e){
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		gotoInfo(){
			let to = this.userInfo === "" ? "../login/login" : "../meInfo/meInfo";
			console.log(to);
			uni.navigateTo({
				url:to
			})
		},
		gotoSetting(){
			uni.navigateTo({
				url:"../setting/setting",
			})
		},
		gotoBuffer(){
			uni.navigateTo({
				url:"../buffer/buffer"
			})
		}
	}
}
